<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getvendor"><i class="fa fa-search">&nbsp;</i></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"><i class="fa fa-plus">&nbsp;</i></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="vendorData" highlight-current-row v-loading="listLoading" @selection-change="selsChange"  border style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			
			<el-table-column prop="vendorCode" label="" width="120" align="center" >
			</el-table-column>
			<el-table-column prop="vendorName" label="" width="180" align="center" >
			</el-table-column>
			<el-table-column prop="corporateRepresentative" label="" width="110" align="center" >
			</el-table-column>
			<el-table-column prop="inCharge" label="" width="120" align="center" >
			</el-table-column>
			<el-table-column prop="phoneNumber" label="" width="95" align="center">
			</el-table-column>
			<el-table-column prop="fax" label="" width="120" align="center" >
			</el-table-column>
			<el-table-column prop="address" label="" min-width="100" align="center" >
			</el-table-column>
			<el-table-column label="" width="140" align="center">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<el-col :span="24" class="toolbar" style="padding: 5px;">
			<el-button type="danger" @click="batchRemove"  :disabled="this.sels.length===0">
				<i class="fa fa-trash" aria-hidden="true"></i>&nbsp;</el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

	<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="vendorCode">
					<el-input v-model="addForm.vendorCode" auto-complete="off" 
					placeholder=""	></el-input>
				</el-form-item>
				<el-form-item label="" >
					<el-input v-model="addForm.vendorName" auto-complete="off" 
					placeholder=""	></el-input>
				</el-form-item>
				<el-form-item label="" >
					<el-input v-model="addForm.corporateRepresentative" auto-complete="off" 
					placeholder=""	></el-input>
				</el-form-item>
				<el-form-item label="" >
					<el-input v-model="addForm.inCharge" auto-complete="off" 
					placeholder=""	></el-input>
				</el-form-item>
				<el-form-item label="" >
					<el-input v-model="addForm.phoneNumber" auto-complete="off" 
					placeholder=""	></el-input>
				</el-form-item>
				<el-form-item label="" >
					<el-input v-model="addForm.fax" auto-complete="off" 
					placeholder=""	></el-input>
				</el-form-item>
				<el-form-item label="" >
					<el-input v-model="addForm.address" auto-complete="off" 
					placeholder=""	></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
		
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="vendorCode">
					<el-input v-model="editForm.vendorCode" auto-complete="off" disabled
					placeholder=""	></el-input>
				</el-form-item>
				<el-form-item label="" >
					<el-input v-model="editForm.vendorName" auto-complete="off" 
					placeholder=""	></el-input>
				</el-form-item>
				<el-form-item label="" >
					<el-input v-model="editForm.corporateRepresentative" auto-complete="off" 
					placeholder=""	></el-input>
				</el-form-item>
				<el-form-item label="" >
					<el-input v-model="editForm.inCharge" auto-complete="off" 
					placeholder=""	></el-input>
				</el-form-item>
				<el-form-item label="" >
					<el-input v-model="editForm.phoneNumber" auto-complete="off" 
					placeholder=""	></el-input>
				</el-form-item>
				<el-form-item label="" >
					<el-input v-model="editForm.fax" auto-complete="off" 
					placeholder=""	></el-input>
				</el-form-item>
				<el-form-item label="" >
					<el-input v-model="editForm.address" auto-complete="off" 
					placeholder=""	></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
export default {
		data() {
			return {
				filters: {
					name: ''
				},
				vendorData: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					vendorCode: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					vendorCode: '',
					vendorName: '',
					corporateRepresentative:'',
					inCharge:'',
					phoneNumber:'',
					fax:'',
					address:'',
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					vendorCode: [
						{ required:true, message: '!', trigger: 'blur' }
					]
				},
				//
				addForm: {
					vendorCode: '',
					vendorName: '',
					corporateRepresentative:'',
					inCharge:'',
					phoneNumber:'',
					fax:'',
					address:'',
				}

			}
		},
		methods:{
			handleCurrentChange(val) {
				this.page = val;
				this.getvendor();
			},
			getvendor(){
				console.log(this.filters.name)
				this.$http.get('/api/vendor/getAllvendor', 
					{params: {name:this.filters.name}}
       				).then((response) => {
          				this.total=response.data.length
          				this.vendorData = response.data
          				this.vendorData = this.vendorData.filter((u, index) => index < 10 * this.page && index >= 10 * (this.page - 1)) //
          				}, response => {
    							// error callback
	    							this.logining = false
	    							this.$notify({
						          	title: '',
						          	message: '!',
						          	type: 'warning'
						            })
  								})
          			
			},
			selsChange: function (val) {
				this.sels = val
				//console.log(this.sels)
			},
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			batchRemove: function () {
				var codes = this.sels.map(x => x.vendorCode);
				console.log(codes);
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					let count = 0;
					for(let i=0;i<codes.length;i++){
						let para = { vendorcode: codes[i] };
						this.$http.post('/api/vendor/delVendor', para).then(
						(response)=>{
							if(response.data.affectedRows){
								count++	
							}
						}
					)
					}
					this.listLoading = false
					this.$notify({
									title: '',
									message: '',
									type: 'success'
								});
					this.getvendor()
				}).catch(() => {

				});
			},
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					let para = { vendorcode: row.vendorCode };
					this.$http.post('/api/vendor/delVendor', para).then(
						(response)=>{
							if(response.data.affectedRows){
								console.log(response.data.affectedRows)
								this.listLoading = false
								this.$notify({
									title: '',
									message: '',
									type: 'success'
								});
								this.getvendor()
							}
						}
					)
				}).catch(() => {

				});
			},
			
			
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					vendorCode: '',
					vendorName: '',
					corporateRepresentative:'',
					inCharge:'',
					phoneNumber:'',
					fax:'',
					address:'',
				};
				
			},
			
			
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							var add = this.addForm;
							var addlist = Object.assign({}, add);
							//console.log(addlist)
          				this.$http.post('/api/vendor/addVendor', 
						addlist).then((response) => {
          					this.addLoading = false
          					this.$notify({
									title: '',
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getvendor();
          				}, response => {
    							// error callback
	    							this.logining = false
	    							this.$notify({
						          	title: '',
						          	message: '!',
						          	type: 'warning'
						            })
  								})
						});
					}else {
           				this.$notify({
			          	title: '',
			          	message: '!',
			          	type: 'warning'
			            })
            			return false
          						}
				});
			},
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							var edit = this.editForm;
							var editlist = Object.assign({}, edit);
          				this.$http.post('/api/vendor/editVendor', 
          				editlist).then((response) => {
          					this.editLoading = false
          					this.$notify({
									title: '',
									message: '',
									type: 'success'
								});
								this.editFormVisible = false;
								this.getvendor();
          				}, response => {
    							// error callback
	    							this.logining = false
	    							this.$notify({
						          	title: '',
						          	message: '!',
						          	type: 'warning'
						            })
  								})
						});
					}else {
           				this.$notify({
			          	title: '',
			          	message: '!',
			          	type: 'warning'
			            })
            			return false
          			}
				});
			},
		},
		mounted() {
			this.getvendor();
			
		}
	}

</script>

<style scoped>
.el-button + .el-button {
    margin-left: 0px;
}
</style>


