import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Login from '@/components/Login'
import Home from '@/components/Home'
import Goods from '@/components/page/navJibenxinxi/goods'
import Satff from '@/components/page/navJibenxinxi/staff'
import Vendor from '@/components/page/navJibenxinxi/vendor'
import Setting from '@/components/page/navXitongshezhi/setting'
import Stock from '@/components/page/navKucunguanli/stock'
import Inbound from '@/components/page/navKucunguanli/inbound'
import Stockchart from '@/components/page/navChart/stockchart'
import Goodslocation from '@/components/page/navChart/goodslocation'

Vue.use(Router)

export default new Router({
	//mode: 'history',
  routes: [

   
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa fa-th',
        children: [
            { path: '/stock', component: Stock, name: '',iconCls:'fa fa-flask' },
            { path: '/inbound', component: Inbound, name: '',iconCls:'fa fa-archive' ,}
        ]
    },
   	{
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-area-chart',
        children: [
            { path: '/stockchart', component: Stockchart, name: '',iconCls: 'fa fa-bar-chart'},
            { path: '/goodslocation', component: Goodslocation, name: '',iconCls: 'fa fa-pie-chart' },
            //{ path: '/user', component: Page1, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-database',
        children: [
            { path: '/goods', component:Goods, name: '',iconCls:'fa fa-shopping-basket' },
            { path: '/staff', component:Satff, name: '',iconCls:'fa fa-address-card-o' },
            { path: '/vendor', component:Vendor, name: '',iconCls:'fa fa fa-truck' },
        ]
      },
    {
    	path:'/login',
    	name:'login',
    	component:Login,
    	hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-gear',
        leaf: true,//
        children: [
            { path: '/setting', component:Setting, name: '' }
        ]
    }
  ]
})



